$size: 60px;
.game_board {
    margin: 0 auto;
    width: $size*6;
    height: $size*6;
    font-size: 0;
    position:relative;
    border: 5px cyan solid;
}
.square{
    width: $size;
    height: $size;
    display: inline-block;
    position:absolute;
    &.violet{
        background:#6e75f8;
    }
    &.light_orange{
        background:#fdbb31;
    }
    &.light_blue{
        background:#45c8fe;
    }
    &.dark_blue{
        background:#1858b8;
    }
    &.dark_orange{
        background:#f77d36;
    }
    &:hover{
        transform-origin: 50% 50%;
        transition: transform 0.2s;
        transform: scale(1.2);
        z-index: 1;
        -webkit-box-shadow: 0px 0px 43px -8px rgba(0,0,0,0.75);
        -moz-box-shadow: 0px 0px 43px -8px rgba(0,0,0,0.75);
        box-shadow: 0px 0px 43px -8px rgba(0,0,0,0.75);
    }
    
}
div[class*='row']{
    position:absolute;
    width: 100%;
    height:$size;

}
@for $i from 0 to 6{
        .row-#{$i}{
            top:$i*60px;
        }
        .square[data-position="#{$i}"]{
             left:$i*60px;
        }
}